import Vue from "vue";
import Vuex from "vuex";

import { texts } from "@/service/translation";
import apiStore from "@/x-vue/services/api.store";
import VueRouter from "vue-router";
import {
  ApiStore,
  CategoryBanners,
  CommentModel,
  PostModel,
  RequestData,
} from "@/x-vue/services/interfaces";
import { AppService } from "@/service/app.service";
import { ApiService } from "@/x-vue/services/api.service";
import { Err } from "@/x-vue/services/defines";

Vue.use(Vuex);

// source https://github.com/Maronato/vue-toastification/tree/master
import Toast from "vue-toastification";
import "vue-toastification/dist/index.css";
import { FirebaseService } from "@/x-vue/services/firebase.service";
Vue.use(Toast);

// Add app default translation
apiStore.texts = texts;

interface AppState extends ApiStore {
  count: number;
  // eslint-disable-next-line  @typescript-eslint/no-explicit-any
  vm: any;
  router: VueRouter;
  posts: PostModel[];
  // the post on current view page.
  post: PostModel;
  // the category of current page. It will be updated on list, view, edit page.
  // Useage - When currencyCategory changes, the app can show difference banner for that category.
  currentCategory: string;
  // This becomes true if the post is being loaded.
  postLoading: boolean;
  banners: CategoryBanners;
}

// Add your state here !
const state: AppState = Object.assign(
  {},
  {
    count: 0,
    // eslint-disable-next-line  @typescript-eslint/no-explicit-any
    vm: {} as any,
    router: {} as VueRouter,
    posts: [],
    post: new PostModel(),
    currentCategory: "",
    postLoading: false,
    banners: {},
  },
  apiStore
);

export default new Vuex.Store({
  state: state,
  mutations: {
    // This method will be called after the user logged in.
    // Do whatever here to initialize user data.
    login: () => {
      ApiService.instance.myCafe();
      if (FirebaseService.instance.token) {
        ApiService.instance.saveToken(
          FirebaseService.instance.token,
          location.hostname
        );
      }
    },
    register: () => {
      ApiService.instance.myCafe();
      if (FirebaseService.instance.token) {
        ApiService.instance.saveToken(
          FirebaseService.instance.token,
          location.hostname
        );
      }
    },
    // Do whatever here to uninitialize user data.
    logout: (state) => {
      state.myCafe = [];
    },
    increment: async (state): Promise<void> => {
      state.count++;
    },
    edit: async (state, post: PostModel & CommentModel): Promise<void> => {
      // if it's comment, then set edit mode
      if (post.parentIdx) {
        post.inEdit = true;
      } else {
        AppService.instance.open(`/edit/${post.idx}` + location.search);
      }
    },
    delete: async (state, model: PostModel & CommentModel): Promise<void> => {
      const api = ApiService.instance;
      const conf = await api.confirm(
        model.isPost ? "Post Delete" : "Comment Delete",
        `Are you sure you want to delete this comment? [IDX]: ${model.idx}`
      );
      if (!conf) return;
      try {
        let res;
        if (model.isPost) {
          res = await api.postDelete(model.idx);
        } else {
          res = await api.commentDelete(model.idx);
        }
        model = Object.assign(model, res);
      } catch (e) {
        ApiService.instance.error(e);
      }
    },
    onMessage: async (
      state,
      payload: Record<string, Record<string, unknown>>
    ) => {
      console.log("store.commit('onMessage'", payload);
      const notification = payload.notification;
      const data = payload.data ? payload.data : {};
      let url = "";
      if (ApiService.instance.user.idx === data["senderIdx"]) return;

      /**
       * If the type is post then move it to a specific post.
       */
      if (data["type"] === "post") {
        url = "/" + data["idx"];
      }

      /**
       * If the type is chat then move it to private chat.
       */
      if (data["type"] === "chat") {
        url = "/chat/" + data["idx"];
      }

      state.vm.$toast(`${notification.title} \n ${notification.body}`, {
        timeout: 10000,
        closeOnClick: false,
        position: "bottom-right",
        onClick: () => {
          state.router.push(url);
        },
      });
    },
    refreshProfile: async (state): Promise<void> => {
      state.user = await ApiService.instance.refreshLoginUserProfile();
    },
    /// Load a cafe category and save into state.cafe
    loadCafe: async (state, domain: string) => {
      const options: RequestData = {
        domain: domain,
      };

      try {
        // console.log(options);
        state.cafe = await ApiService.instance.cafeGet(options);
        console.log(state.cafe);
      } catch (e) {
        if (e === Err.main_cafe_has_no_cafe_category_record) {
          // If the cafe category record does not exists,
          // then, it might be the root site of the cafe is not being created.
          // this.app.error(e);
          console.log(e);
        } else if (e === Err.cafe_not_exists) {
          ApiService.instance.error(
            "@todo set dev environment to move to local main cafe"
          );
        } else {
          ApiService.instance.error(e);
        }
      }
    },
    cafeUpdate: async (state, options) => {
      try {
        console.log("options; ", options);
        state.cafe = await ApiService.instance.cafeUpdate(options);
        console.log("cafeUpdate; ", state.cafe);
        ApiService.instance.alert("Cafe Update", "Cafe update success");
      } catch (e) {
        ApiService.instance.error(e);
      }
    },
    loadBanners: async (state, cafeDomain: string) => {
      if (cafeDomain.indexOf(".") > -1) {
        cafeDomain = cafeDomain.split(".")[0];
      }

      // console.log("loadBanners", state, cafeDomain);
      const banners = await ApiService.instance.loadBanners(cafeDomain);
      // console.log("Banners;", banners);
      const _banners: CategoryBanners = {};
      if (banners && banners.length) {
        for (const banner of banners) {
          if (!_banners[banner.subcategory]) _banners[banner.subcategory] = {};
          if (!_banners[banner.subcategory][banner.code]) {
            _banners[banner.subcategory][banner.code] = [];
          }

          _banners[banner.subcategory][banner.code].push({
            bannerUrl: banner.bannerUrl,
            clickUrl: banner.clickUrl,
            idx: banner.idx,
            title: banner.title ?? "",
          });
        }
        /// Make it reactive.
        console.log("Banners :", _banners);
        state.banners = Object.assign({}, state.banners, _banners);
      }
    },
    postGet: async (state) => {
      // console.log("PostView::mounted(); ");
      state.postLoading = true;

      const arr = location.href.split("/");
      const last = arr[arr.length - 1];
      const options: RequestData = {
        idx: last,
      };

      try {
        state.post = await ApiService.instance.postGet(options);
        // console.log("store.state.post;", state.post);

        state.currentCategory = state.post.categoryId;
        state.postLoading = false;
      } catch (e) {
        state.postLoading = false;
        ApiService.instance.error(e);
      }
    },
    openToast: (state) => {
      state.vm.$toast("You have a toast message \n Hi! welcome to.....", {
        timeout: 5000,
        closeOnClick: false,
        position: "bottom-right",
        onClick: () => {
          console.log("onclick");
          state.router.push("1");
        },
        onClose: () => {
          console.log("onclose");
        },
      });
    },
  },
  actions: {},
  modules: {},
});
